.header {
  text-align: center;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: auto;
  max-width: 1600px;
  max-height: 216px;
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.56),
      rgba(0, 0, 0, 0.56)
    ),
    url('/src/img/main-img320@1x.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 2dppx),
    (min-resolution: 192dpi) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('/src/img/main-img320@2x.jpg');
  }
  @media screen and (min-width: 768px) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('/src/img/main-img768@1x.jpg');
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 2dppx),
      (min-resolution: 192dpi) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('/src/img/main-img768@2x.jpg');
    }
  }
  @media screen and (min-width: 1280px) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('/src/img/main-img1280@1x.jpg');

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 2dppx),
      (min-resolution: 192dpi) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('/src/img/main-img1280@2x.jpg');
    }
  }
}
.header__lib {
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.56),
      rgba(0, 0, 0, 0.56)
    ),
    url('/src/img/library-img320@1x.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 2dppx),
    (min-resolution: 192dpi) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('/src/img/library-img320@2x.jpg');
  }
  @media screen and (min-width: 768px) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('/src/img/library-img768@1x.jpg');
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 2dppx),
      (min-resolution: 192dpi) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('/src/img/library-img768@2x.jpg');
    }
  }
  @media screen and (min-width: 1280px) {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('/src/img/library-img1280@1x.jpg');

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 2dppx),
      (min-resolution: 192dpi) {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('/src/img/library-img1280@2x.jpg');
    }
  }
}
.header__wrap {
    position: relative;
}
.header__container {
    padding-top: 42px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (min-width: 768px) {
        padding-left: 31px;
        padding-right: 33px;
        padding-bottom: 40px;
    }
}
.header__logo-container {
    margin-right: auto;
    align-items: center;

}
.header__logo-icon {
  fill: #ffffff;
}
.header__logo {
    color: #FFFFFF;
    @media screen and (min-width: 768px) {
        padding-right: 8px;
    }
    
}
.logo {
    display: none;
}

.header__nav {
    align-items: center;
}
.header__nav:last-child {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.header__site-nav {
    display: flex;
    justify-content: flex-end;
}

.header__item {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.1;

        text-transform: uppercase;

        &:not(:last-child) {
            margin-right: 39px;
        }
}
.header__link {
    display: block;
    color: #ffffff;
    position: relative;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    text-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.underline {
    border-radius: 2px;

    &::after {
        color: #ff001b;
        z-index: 1000;
        display: block;
        position: absolute;
        content: '';
        width: 100%;
        height: 3px;
        left: 0;
        bottom: -5px;
        background-color: #ff001b;
        // opacity: 1;
        transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

}
.search__box {
    width: auto;
    position: relative;
    top: 56px;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-bottom: 92px;
}

.search__input {
    color: #ffffff;
    min-width: 280px;
    background-color: transparent;
    outline: 0;
    border-width: 0 0 0.5px;
    border-color: rgb(255, 255, 255);

    @media screen and (min-width: 768px) {
        min-width: 336px;
    }
    @media screen and (min-width: 1200px) {
        min-width: 394px;
    }
}
.search__input::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.1;
    display: flex;
    align-items: center;
}
.search__button {
    position: fixed;
    top: 10px;
    left: calc(50% + 140px - 10px);
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    overflow: hidden;
    
    @media screen and (min-width: 768px) {
        left: calc(50% + 168px - 10px);
        }
       
    @media screen and (min-width: 1200px) {
        left: calc(50% + 197px - 10px);
        }
}

.form__text {
    margin-top: 10px;
    color: rgba(255, 0, 27, 1);
    font-weight: 400;
    font-size: 14px;
    line-height: 1.1;
    font-family: 'Roboto';
    font-style: normal;
}

@media screen and (min-width: 768px) {
    .logo {
        display: none;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 500;
        font-size: 30px;
        line-height: 35px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #ffffff;
    }
    .header__link-icon:not(:last-child) {
        margin-right: 12px;
    }
    
    .header {
        width: 100%;
    }
    .header__conteiner {
    padding-top: 45px;
  }

  .header__logo-container {
    display: flex;
  }    
}



.library__nav {
    display: flex;
    gap: 20px;
    padding-bottom: 62px;
    justify-content: center;

    @media screen and (min-width: 768px) {
        gap: 32px;
        padding-bottom: 60px;
    }
    @media screen and (min-width: 1280px) {
        gap: 16px;
    }

}

.library__nav-btn {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  transition: background-color linear 250ms;

    color: #FFFFFF;
    box-sizing: border-box;
    width: 130px;
    height: 42px;
    border: 1px solid #FFFFFF;
    border-radius: 5px;
    background-color: transparent;

    @media screen and (min-width: 768px) {
        width: 152px;
    }
    @media screen and (min-width: 1280px) {
        width: 147px;
    }

}

.library__nav-btn:hover, .is-active {
  background-color: #ff6b01;
  border-color: #ff6b01 ;
}


.footer {
  // position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #f7f7f7;
  color: #545454;
}

.footer__line {
  text-align: center;
  padding: 29px auto;
}

.footer__icon {
  fill: #ff6b08;
}
.footer__icon-blick {
  scale: 1.3;
}
.footer__copyright {
  font-family: 'Roboto';
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  margin-top: 28px;
  margin-bottom: 10px;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 19px;
  }
}

.footer__dev-link {
  font-family: 'Roboto';
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  margin-bottom: 30px;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 19px;
  }
}